.auth {
    width: 300px;
    margin: 140px auto;
    padding: 2rem;
    border: 1px solid #ccc;
    border-radius: 8px;
    text-align: center;
  }
  
  .auth h1 {
    margin-bottom: 1rem;
  }
  
  .auth form {
    display: flex;
    flex-direction: column;
    margin: 0 auto; /* Center the form horizontally */
  }
  
  .auth .control {
    margin-bottom: 1rem;
  }
  
  .auth .control label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .auth .control input {
    width: 100%;
    padding: 0.5rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .auth .actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .auth .toggle {
    font-size: 0.8rem;
    color: #666;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  
  .auth button {
    padding: 0.5rem 1rem;
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .auth button:hover {
    background-color: #0056b3;
    color: #ececec;
  }
  
  .auth button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  