/* NavBar.module.css */

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: #333;
    color: #fff;
  }
  
  .left-links, .right-links {
    display: flex;
  }
  
  .left-links > * {
    margin-right: 1rem;
    color: #fff; /* Setting color for left links */
  }
  
  .right-links > * {
    margin-left: 1rem;
  }
  
  /* Styling for the Logout button */
  button {
    background-color: #555;
    color: #fff;
    border: none;
    padding: 0.5rem 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #777;
  }
  
  /* Styling for nav links */
  a {
    color: #fff; /* Setting default color for nav links */
    text-decoration: none; /* Removing underline */
    margin-right: 1rem; /* Adding margin between nav links */
  }
  
  a:hover {
    color: #ffa500; /* Changing color on hover */
  }
  