/* ProfileForm.module.css */

.formContainer {
    max-width: 400px;
    margin: 80px auto;
  }
  
  .formContainer h2 {
    margin-bottom: 1rem;
  }
  
  .formContainer form {
    display: flex;
    flex-direction: column;
  }
  
  .formContainer label {
    margin-bottom: 0.5rem;
  }
  
  .formContainer input[type="text"],
  .formContainer input[type="url"] {
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .formContainer button {
    padding: 0.5rem 1rem;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .formContainer button:hover {
    background-color: #0056b3;
  }
  